body#login {
    position: absolute;
    background-image: url("https://picsum.photos/1600/900");
    font-family: 'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif;
    height: 100%;
    width: 100%;
    margin: 0;


}

.maincontainer {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translateX(-50%) translateY(-50%);
}

.container3 input {

    padding: 10px;
    border-radius: 10px;
}

img {
    background: none;
    width: 100px;
}

.container1 {

    top: 10rem;
    left: 10rem;
    width: 20rem;
    height: 25rem;
    background-color: wheat;
    justify-content: center;
    text-align: center;
    border-radius: 50% 50% 50% 50%;
    opacity: 0.7;
    border-style: dotted;

}

.container2 {
    position: absolute;
    top: 0rem;
    left: 0rem;
    width: 20rem;
    height: 25rem;
    justify-content: center;
    text-align: center;

}

button {
    padding: 5px;
    border-radius: 5px;
    color: blueviolet;
    font-weight: bold;
}